<template>
  <section class="user">
    <div class="user_options-container">
      <div class="user_options-text">
        <div class="user_options-unregistered">
          <h2 class="user_unregistered-title">ETL</h2>
          <p class="user_unregistered-text">
            ETLExtraction-Trasformation-Loading
          </p>
          <button class="user_unregistered-signup" id="signup-button">
            
          </button>
        </div>

        <div class="user_options-registered">
          <h2 class="user_registered-title">ETL</h2>
          <p class="user_registered-text">
            ETLExtraction-Trasformation-Loading
          </p>
          <button class="user_registered-login" id="login-button"></button>
        </div>
      </div>

      <div class="user_options-forms" id="user_options-forms">
        <div class="user_forms-login">
          <h2 class="forms_title">Login</h2>
          <form class="forms_form">
            <fieldset class="forms_fieldset">
              <div class="forms_field">
                <input
                  type="text"
                  placeholder=""
                  class="forms_field-input"
                  required
                  autofocus
                  v-model="username"
                />
              </div>
              <div class="forms_field">
                <input
                  type="password"
                  placeholder="Password"
                  class="forms_field-input"
                  required
                  v-model="password"
                />
              </div>
            </fieldset>
            <div class="forms_buttons">
              <button type="button" class="forms_buttons-forgot" @click="showForgotPwd">
                ?
              </button>
              <input
                type="submit"
                value=""
                class="forms_buttons-action"
                @click.prevent="handleSubmit"
              />
            </div>
          </form>
        </div>
        <div class="user_forms-signup">
          <h2 class="forms_title">Sign Up</h2>
          <form class="forms_form">
            <fieldset class="forms_fieldset">
              <div class="forms_field">
                <input
                  type="text"
                  placeholder=""
                  class="forms_field-input"
                  required
                  v-model="username"
                />
              </div>
              <div class="forms_field">
                <input
                  type="password"
                  placeholder=""
                  class="forms_field-input"
                  required
                  v-model="password"
                />
              </div>
              <div class="forms_field">
                <input
                  type="password"
                  placeholder=""
                  class="forms_field-input"
                  required
                  v-model="rPassword"
                />
              </div>
            </fieldset>
            <div class="forms_buttons">
              <input
                type="submit"
                value=" "
                class="forms_buttons-action"
                @click.prevent="handleRegister"
              />
            </div>
          </form>
        </div>
      </div>
    </div>
    <ForgotPwd :visible="visible" @close="()=>{this.visible = false}"></ForgotPwd>
  </section>
</template>

<script>
import './style.scss'
import fetch from '@/services/fetch'
import ForgotPwd from '@/components/forgot-pwd'
export default {
  components: {
    ForgotPwd
  },
  data () {
    return {
      username: '',
      password: '',
      rPassword: '',
      visible: false
    }
  },
  mounted () {
    /**
     * DOM
     */
    const signupButton = document.getElementById('signup-button')
    const loginButton = document.getElementById('login-button')
    const userForms = document.getElementById('user_options-forms')

    /**
     * ""
     */
    signupButton.addEventListener(
      'click',
      () => {
        this.reset()
        userForms.classList.remove('bounceRight')
        userForms.classList.add('bounceLeft')
      },
      false
    )

    /**
     * ""
     */
    loginButton.addEventListener(
      'click',
      () => {
        this.reset()
        userForms.classList.remove('bounceLeft')
        userForms.classList.add('bounceRight')
      },
      false
    )
  },
  methods: {
    // 
    reset () {
      this.username = ''
      this.password = ''
      this.rPassword = ''
    },
    // 
    async handleSubmit () {
      let res = await fetch.post('/login', {
        username: this.username,
        password: this.password
      })
      if (!res.exception) {
        localStorage.setItem('userInfo', JSON.stringify(res.data))
        this.$message.success('')
        this.$router.push({ path: '/' })
        localStorage.setItem('defaultSelectedKeys', JSON.stringify(['1']))
      } else {
        this.$message.error(res.exception)
        this.reset()
      }
      return false
    },
    // 
    async handleRegister () {
      if (this.password !== this.rPassword) {
        this.$message.error('')
        this.reset()
        return
      } else if (this.password.length < 6) {
        this.$message.error('6')
        this.reset()
        return
      }

      let res = await fetch.post('/register', {
        username: this.username,
        password: this.password
      })
      if (!res.exception) {
        this.$message.success('')
        this.reset()
        const userForms = document.getElementById('user_options-forms')
        userForms.classList.remove('bounceLeft')
        userForms.classList.add('bounceRight')
      } else {
        this.$message.error(res.exception)
        this.reset()
      }
    },
    // 
    showForgotPwd () {
      this.visible = true
    }
  }
}
</script>
