<template>
  <div class="report-detail">
    <a-descriptions title="" bordered>
      <a-descriptions-item label="">{{desc.deploymentName}}</a-descriptions-item>
      <a-descriptions-item label="">{{desc.sourceConfigureName}}</a-descriptions-item>
      <a-descriptions-item label="">{{desc.targetConfigureName}}</a-descriptions-item>
      <a-descriptions-item label="">{{desc.processName}}</a-descriptions-item>
      <a-descriptions-item label="">{{desc.startUserName}}</a-descriptions-item>
      <a-descriptions-item label="">{{desc.startUserName}}</a-descriptions-item>
      <a-descriptions-item label="">{{desc.stopUserName || ''}}</a-descriptions-item>
      <a-descriptions-item label="">
        {{formatDateTime(desc.startTime)}}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{formatDateTime(desc.endTime)}}
      </a-descriptions-item>
      <a-descriptions-item label="">
        {{formatDateTime(desc.recordTime)}}
      </a-descriptions-item>
      <a-descriptions-item label="">
        <a-tag :color="getStateColor(desc.state)">{{desc.state}}</a-tag>
      </a-descriptions-item>
      <a-descriptions-item label="" style="color:red;">
        <span v-if="desc.throwable && desc.throwable.length > 10">
          {{desc.throwable.slice(0, 10) + '...'}}
          <a @click="showDetail(desc.throwable)"></a>
        </span>
        <span v-else>{{desc.throwable || ''}}</span>
      </a-descriptions-item>
      <a-descriptions-item label="" :span="3">
        <a-row :gutter="16" style="margin-bottom:15px;">
          <a-col :span="24">
            <a-statistic title=" / " :value="desc.finishTasks">
              <template v-slot:suffix>
                <span> / {{desc.taskNum}}</span>
              </template>
            </a-statistic>
          </a-col>
        </a-row>
        <a-row :gutter="16" style="margin-bottom:15px;">
          <a-col :span="6">
            <a-statistic title="" :value="desc.readSucceedRecords">
              <template v-slot:suffix>
                <span></span>
              </template>
            </a-statistic>
          </a-col>
          <a-col :span="6">
            <a-statistic title="" :value="formatBytes(desc.readSucceedBytes)">
              <template v-slot:suffix>
                <span>Bytes</span>
              </template>
            </a-statistic>
          </a-col>
          <a-col :span="6">
            <a-statistic title="" :value="desc.writeSucceedRecords">
              <template v-slot:suffix>
                <span></span>
              </template>
            </a-statistic>
          </a-col>
          <a-col :span="6">
            <a-statistic title="" :value="formatBytes(desc.writeSucceedBytes)">
              <template v-slot:suffix>
                <span>Bytes</span>
              </template>
            </a-statistic>
          </a-col>
        </a-row>
        <a-row :gutter="16" style="margin-bottom:15px;">
          <a-col :span="6">
            <a-statistic title="ETL" :value="desc.totalInputEtlRecords">
              <template v-slot:suffix>
                <span></span>
              </template>
            </a-statistic>
          </a-col>
          <a-col :span="6">
            <a-statistic title="ETL" :value="desc.totalOutputEtlRecords">
              <template v-slot:suffix>
                <span></span>
              </template>
            </a-statistic>
          </a-col>
          <a-col :span="6">
            <a-statistic title="" :value="formatBytes(desc.averageByteSpeed)">
              <template v-slot:suffix>
                <span>Bytes/s</span>
              </template>
            </a-statistic>
          </a-col>
          <a-col :span="6">
            <a-statistic title="" :value="desc.averageRecordSpeed">
              <template v-slot:suffix>
                <span>/</span>
              </template>
            </a-statistic>
          </a-col>
        </a-row>
      </a-descriptions-item>
      <a-descriptions-item label="reader" :span="3">
        <pre>
          {{formatJson(desc.sourceConfigureContent)}}
        </pre>
      </a-descriptions-item>
      <a-descriptions-item label="writer" :span="3">
        <pre>
          {{formatJson(desc.targetConfigureContent)}}
        </pre>
      </a-descriptions-item>
      <a-descriptions-item label="" :span="3">
        <pre>
          {{formatJson(desc.processContent)}}
        </pre>
      </a-descriptions-item>
    </a-descriptions>
  </div>
</template>

<script>
import './style.scss'
import fetch from '@/services/fetch'
import config from '@/config/index'
import dateUtils from '@/utils/date'
import formatJson from '@/utils/json'
import formatBytes from '@/utils/bytes'
export default {
  data () {
    return {
      id: '',
      desc: ''
    }
  },
  mounted () {
    if (this.$route.params.id) {
      this.id = this.$route.params.id
      localStorage.setItem('reportId', this.id)
    } else {
      this.id = localStorage.getItem('reportId')
    }
    this.getDesc()
  },
  methods: {
    // 
    getStateColor (state) {
      return config.REPORT_STATE_MAP[state]
    },
    // 
    formatDateTime (timeStamp) {
      return dateUtils.formatDateTime(timeStamp)
    },
    // 
    formatJson (content) {
      return formatJson(content)
    },
    // 
    formatBytes (B) {
      return formatBytes(B)
    },
    // 
    async getDesc () {
      let res = await fetch.post('/getOneJobReport', {
        jobReportId: this.id
      })
      this.desc = res.data
      this.desc.sourceConfigureContent = JSON.parse(this.desc.sourceConfigureContent)
      if (this.desc.sourceConfigureContent.parameter.password) {
        this.desc.sourceConfigureContent.parameter.password = '********'
      }
      this.desc.targetConfigureContent = JSON.parse(this.desc.targetConfigureContent)
      if (this.desc.targetConfigureContent.parameter.password) {
        this.desc.targetConfigureContent.parameter.password = '********'
      }
    },
    // 
    showDetail (info) {
      this.$confirm({
        title: '',
        content: info,
        okText: '',
        cancelText: ''
      })
    }
  }
}

</script>
