<template>
  <a-layout-sider :trigger="null" collapsible v-model="collapsed" class="sider">
    <div class="logo-wrapper">
      <img src="../../../public/logo.png" alt="logo">
    </div>
    <h2 v-if="!collapsed"><span class="highlight">ETL</span> </h2>
    <a-menu
      :defaultSelectedKeys="defaultSelectedKeys"
      :defaultOpenKeys="['sub1','sub2', 'sub3','sub4']"
      @select="saveSelectedKey"
      mode="inline"
      theme="dark"
      :inlineCollapsed="collapsed">
      <a-sub-menu key="sub1">
        <span slot="title"><a-icon type="fund" /><span></span></span>
        <a-menu-item key="1">
          <router-link to="/monitoring-show"></router-link>
          </a-menu-item>
      </a-sub-menu>
      <a-sub-menu key="sub2">
        <span slot="title"><a-icon type="interaction" /><span></span></span>
        <a-menu-item key="2">
          <router-link to="/process-manage"></router-link>
        </a-menu-item>
        <a-menu-item key="3">
          <router-link to="/deployment-manage"></router-link>
        </a-menu-item>
        <a-menu-item key="4">
          <router-link to="/report-show"></router-link>
        </a-menu-item>
      </a-sub-menu>
      <a-sub-menu key="sub3">
        <span slot="title"><a-icon type="cluster" /><span></span></span>
        <a-menu-item key="5">
          <router-link to="/config-manage"></router-link>
        </a-menu-item>
      </a-sub-menu>
      <a-sub-menu key="sub4">
        <span slot="title"><a-icon type="alert" /><span></span></span>
        <a-menu-item key="6">
          <router-link to="/permission-approval"></router-link>
        </a-menu-item>
        <a-menu-item key="7">
          <router-link to="/permission-record"></router-link>
        </a-menu-item>
      </a-sub-menu>
    </a-menu>
  </a-layout-sider>
</template>

<script>
export default {
  props: ['collapsed'],
  data () {
    return {
      defaultSelectedKeys: ['1']
    }
  },
  created () {
    let key = JSON.parse(localStorage.getItem('defaultSelectedKeys')) || ['1']
    this.defaultSelectedKeys = key
  },
  methods: {
    // key
    saveSelectedKey ({ item, key, selectedKeys }) {
      localStorage.setItem('defaultSelectedKeys', JSON.stringify([key]))
    }
  }
}
</script>
